import { styled } from "@mui/material";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import { useState } from "react";
import { CurrentStatusTask } from "./CurrentStatusTask";

const TodoInfo = styled("li")({
  border: "1px solid gray",
  backgroundColor: "#CEFDED",
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  paddingTop: "1vh",
  paddingBottom: "1vh",
});

const Status = styled("div")<{ isActive: boolean }>(({ isActive }) => ({
  background: isActive ? "#fdc2b8" : "#ffb2a4",
  height: "20px",
  minHeight: "2vh",
  padding: "6px",
  minWidth: "108px",
  border: "1px solid gray",
  borderRadius: "16px",
  cursor: "pointer",
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  position: "relative",
  transition: "0.2s",
  "&:hover": {
    background: isActive ? "#ffb2a4" : "#CEFDED",
  },
}));

const TextTodo = styled("span")({
  padding: "1vw",
  fontSize: "14px",
});

export const currentTask = ["Выполнено", "На паузе", "В работе"];

export const TaskItem = () => {
  const [status, setStatus] = useState(false);
  const [currentStatus, setCurrentStatus] = useState(currentTask[0]);

  return (
    <>
      <TodoInfo>
        <TextTodo>12.33.23</TextTodo>
        <TextTodo>Шифр Задачи</TextTodo>
        <TextTodo>123.123</TextTodo>
        <TextTodo>123.123</TextTodo>
        <Status isActive={!status} onClick={() => setStatus(!status)}>
          {currentStatus}
          <KeyboardArrowDownIcon />
          {status && (
            <CurrentStatusTask
              currentTask={currentTask}
              setCurrentStatus={setCurrentStatus}
            />
          )}
        </Status>
        <TextTodo>Lorem ipsum</TextTodo>
        <TextTodo>123.123</TextTodo>
        <TextTodo>123.123</TextTodo>
        <TextTodo>123.123</TextTodo>
        <TextTodo>123.123</TextTodo>
        <TextTodo>123.123</TextTodo>
        <TextTodo>123.123</TextTodo>
        <TextTodo>123.123</TextTodo>
      </TodoInfo>
    </>
  );
};
