import { Box, styled } from "@mui/material";

const Head = styled("div")({
  minHeight: "fit-content",
  alignItems: "center",
  boxShadow: "0 4px 8px -2px rgb(0 0 0 / 0.1)",
  color: "#11142D",
  fontWeight: "600",
  fontSize: "14px",
});

const Menu = styled("div")({
  display: "flex",
  justifyContent: "space-between",
  minHeight: "fit-content",
  backgroundColor: "#F7F7FC",
});

const Title = styled("span")({
  padding: "8px",
  fontSize: "22px",
});

export const Header = () => {
  return (
    <Head>
      <Title>Задачи</Title>
      <Menu>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          height="66px"
          width="99px"
        >
          Дата
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          height="66px"
          width="105px"
        >
          Шифр Задачи
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          height="66px"
          width="105px"
        >
          Шифр Проекта
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          height="66px"
          width="57px"
        >
          Задача
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          height="66px"
          width="141px"
        >
          Статус
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          height="66px"
          width="90px"
        >
          Ответственный
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          height="66px"
          width="100px"
        >
          Переназначить
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          padding="7px"
        >
          Приоритет
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          height="66px"
          width="90px"
        >
          Комментарий
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          height="66px"
          width="90px"
        >
          План время
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          height="66px"
          width="90px"
        >
          Факт время
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          height="66px"
          width="60px"
        >
          Начал
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          height="66px"
          width="90px"
        >
          Завершил
        </Box>
      </Menu>
    </Head>
  );
};
