import { keyframes, styled } from "@mui/material";

const fadeIn = keyframes`
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
`;

const CurrentStatus = styled("ul")({
  listStyle: "none",
  backgroundColor: "#F8F8F8",
  border: "1px solid gray",
  position: "absolute",
  fontSize: "14px",
  left: "10vw",
  minWidth: "130px",
  opacity: "0",
  animationName: `${fadeIn}`,
  animationDuration: "0.7s",
  animationDelay: "0.1s",
  animationFillMode: "forwards",
  borderRadius: "10px",
});

const ListElement = styled("li")({
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  transition: "0.2s",
  borderRadius: "10px",
  "&:hover": {
    background: "#71FACA",
  },
});

interface CurrentStatusTaskProps {
  setCurrentStatus: (value: string) => void;
  currentTask: string[];
}

export const CurrentStatusTask: React.FC<CurrentStatusTaskProps> = ({
  setCurrentStatus,
  currentTask,
}) => {
  return (
    <CurrentStatus>
      {currentTask.map((item, idx) => (
        <ListElement
          key={item}
          onClick={() => setCurrentStatus(currentTask[idx])}
        >
          {currentTask[idx]}
        </ListElement>
      ))}
    </CurrentStatus>
  );
};
