import { styled } from "@mui/material";
import { useState } from "react";
import { Todos } from "./Todos";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";

const MainBlock = styled("div")({});

const List = styled("div")<{ props: boolean }>(({ props }) => ({
  cursor: "pointer",
  borderTop: "1px solid gray",
  boxShadow: "0 4px 8px -2px rgb(0 0 0 / 0.1)",
  transition: "0.2s",
  "&:hover": {
    background: "#F8F8F8",
  },
}));

const TodosName = styled("p")({
  display: "flex",
  padding: "1vw",
  fontWeight: "400",
  fontSize: "14px",
  lineHeight: "20px",
  alignItems: "center",
});

export const TaskList = () => {
  const [upcomingIsOpen, setUpcomingIsOpen] = useState(false);
  const [todayIsOpen, setTodayIsOpen] = useState(true);
  const [completedIsOpen, setCompletedIsOpen] = useState(false);

  const upcomingOpen = () => {
    setUpcomingIsOpen(!upcomingIsOpen);
    setTodayIsOpen(false);
    setCompletedIsOpen(false);
  };

  const todayOpen = () => {
    setTodayIsOpen(!todayIsOpen);
    setUpcomingIsOpen(false);
    setCompletedIsOpen(false);
  };

  const completedOpen = () => {
    setCompletedIsOpen(!completedIsOpen);
    setTodayIsOpen(false);
    setUpcomingIsOpen(false);
  };

  return (
    <MainBlock>
      <List onClick={() => upcomingOpen()} props={upcomingIsOpen}>
        <TodosName>
          Предстоящие задачи
          <KeyboardArrowDownIcon />
        </TodosName>
      </List>
      {upcomingIsOpen && <Todos />}
      <List onClick={() => todayOpen()} props={todayIsOpen}>
        <TodosName>
          Задачи на сегодня
          <KeyboardArrowDownIcon />
        </TodosName>
      </List>
      {todayIsOpen && <Todos />}
      <List onClick={() => completedOpen()} props={completedIsOpen}>
        <TodosName>
          Выполненые задачи
          <KeyboardArrowDownIcon />
        </TodosName>
      </List>
      {completedIsOpen && <Todos />}
    </MainBlock>
  );
};
